@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:opsz,wght@6..12,300;6..12,400;6..12,500;6..12,600;6..12,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter&family=Nunito+Sans:opsz,wght@6..12,300;6..12,400;6..12,500;6..12,600;6..12,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.greet_background{
  background: linear-gradient(rgb(89, 136, 180),rgb(59, 91, 121));
  margin: 0;
  padding: 0;
  min-height: 100vh;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: Nunito Sans;

}
.greet_container{
  margin: 50px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 50px;
  gap: 50px;
  border-radius: 24px;
  max-width: 1000px;
}
.greet_text{
  max-width: 450px;
  width: 100%;
}
.greet_text a{
  font-weight: 600;
  font-size: 24px;
  color: white;
  text-shadow: 0px 0px 5px rgba(0,0,0,0.4);;
}
.greet_start_container{
  background: rgba(255,255,255,0.9);
  padding: 35px;
  border-radius: 18px;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
  justify-content: center;
  text-align: center;
  box-shadow: 0px 0px 20px rgba(255,255,255,0.4);
}
.greet_start_container button{
  width: 300px;
  height: 50px;
  border: none;
  border-radius: 10px;
  font-weight: 1000;
  font-size: 15px;
  color: rgb(36, 36, 36);
  cursor: pointer;
  background: rgb(127, 176, 221);
  font-family: Nunito Sans;
  box-shadow: 0px 0px 5px rgba(0,0,0,0.4);
}
.greet_start_container button:hover{
  background: rgb(110, 157, 201);
}
.greet_start_container a{
  font-weight: 600;
  font-size: 18px;
  max-width: 370px;
}
.footer_container{
  background: rgb(7, 10, 14);
  width: 100%;
  height: 250px;
}


.test-main_container{
    display: flex;
    flex-direction: column;
    height: 100%;
    min-height: 100vh;
    width: 100%;
    max-width: 1000px;
    background: rgba(255,255,255,0.9);
    border-radius: 16px;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
    margin-bottom: 30px;
}
.test-main_container h1{
    text-align: right;
    margin-right: 30px;
}
.profile_btn{
    display: flex;
    
    align-items: center;
    justify-content: end;
    width: 100%;
}
.profile_btn button{
    margin:30px;
    padding: 15px 50px 15px 50px;
    border: none;
    border-radius: 16px;
    font-weight: 1000;
    font-size: 15px;
    color: rgb(36, 36, 36);
    cursor: pointer;
    background: rgb(127, 176, 221);
    font-family: Nunito Sans;
    box-shadow: 0px 0px 5px rgba(0,0,0,0.4);
}
.profile_btn button:hover{
    background: rgb(110, 157, 201); 
}
.profile_container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}
.profile_container h2{
    background: rgb(127, 176, 221);
    width: 100%;
    text-align: center;
    padding: 35px 0 35px 0;
}

.profile_inputs{
    display: flex;
    flex-direction: column;
    align-items: end;
}
.profile_input{
    display: flex;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    
}
.profile_input input{
    font-weight: 600;
    font-size: 16px;
    text-align: center;
    height: 25px;
    width: 100px;
    border: none;
    border-bottom: 1px solid rgba(59, 91, 121,1);  
    background: transparent;
}
.test-questions-container{
    display: flex;
    flex-direction: column;
    background: rgba(110, 157, 201,0.1);
    border-radius: 12px;
    padding: 50px;
    grid-gap: 20px;
    gap: 20px;
}
.result-container{
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    padding: 50px;
    grid-gap: 20px;
    gap: 20px;
}
.test-question-container{
    
    border-radius: 12px;
}
.test_question-text{
    padding: 35px 0px 35px 0px;
    background: linear-gradient(rgb(127, 176, 221), rgb(127, 185, 240));
    height: 100%;
    font-weight: 600;
    border-radius: 20px;
}
.test-answers-container{
    padding: 20px;
    height: 100%;
    border-bottom:1px solid rgba(59, 91, 121,0.2);
    border-start-end-radius: 12px;
    border-end-end-radius: 12px;
    border-left: 4px solid rgba(110, 157, 201,0);
}
.test-answers-container:hover{
    background: rgba(123, 191, 255, 0.1);
    cursor: pointer;
    border-left: 4px solid rgba(59, 91, 121,0.3);
}
.test-answers-selected{
    background: rgb(172, 204, 235);
    border-left: 4px solid rgb(59, 91, 121);
    font-weight: 600;
}
.test-answers-selected:hover{
    background: rgb(172, 204, 235);
    border-left: 4px solid rgb(59, 91, 121);
    font-weight: 600;
}
.test-answers-unselected{
    color:gray;
}
.test-test{
    color:red;
}
.test_question-text h3{
    margin-left: 20px;
}
.red{
    color:red;
    
}
.profile_container a{
    margin-bottom: 20px;
}
.test-disable{
    display: none;
}
